export interface WykresXYDane {
  label: string;
  dane0?: number;
  dane1?: number;
  dane2?: number;
  dane3?: number;
  dane4?: number;
  dane5?: number;
  dane6?: number;
  dane7?: number;
  dane8?: number;
  dane9?: number;
}

function xor(a: boolean, b: boolean) {
  return ( a && !b ) || ( !a && b )
}

function max(v: WykresXYDane, serie: string[], prawy: boolean) {
  let max = 0;
  if (serie[0] && xor(prawy, serie[0].indexOf(":R") === -1) && v.dane0 && v.dane0 > max) max = v.dane0;
  if (serie[1] && xor(prawy, serie[1].indexOf(":R") === -1) && v.dane1 && v.dane1 > max) max = v.dane1;
  if (serie[2] && xor(prawy, serie[2].indexOf(":R") === -1) && v.dane2 && v.dane2 > max) max = v.dane2;
  if (serie[3] && xor(prawy, serie[3].indexOf(":R") === -1) && v.dane3 && v.dane3 > max) max = v.dane3;
  if (serie[4] && xor(prawy, serie[4].indexOf(":R") === -1) && v.dane4 && v.dane4 > max) max = v.dane4;
  if (serie[5] && xor(prawy, serie[5].indexOf(":R") === -1) && v.dane5 && v.dane5 > max) max = v.dane5;
  if (serie[6] && xor(prawy, serie[6].indexOf(":R") === -1) && v.dane6 && v.dane6 > max) max = v.dane6;
  if (serie[7] && xor(prawy, serie[7].indexOf(":R") === -1) && v.dane7 && v.dane7 > max) max = v.dane7;
  if (serie[8] && xor(prawy, serie[8].indexOf(":R") === -1) && v.dane8 && v.dane8 > max) max = v.dane8;
  if (serie[9] && xor(prawy, serie[9].indexOf(":R") === -1) && v.dane9 && v.dane9 > max) max = v.dane9;
  return max;
}

export function jednostkiAndDivider(dane_xy: WykresXYDane[], serie: string[], jednostka: string, jednostka_prawa_os: string, pc_divider = 1) {
  let jL = jednostka;
  let jR = jednostka;
  if (jednostka && jednostka?.indexOf("auto") > -1) {
    let maxL = 0;
    dane_xy?.forEach(v => {
      maxL = Math.max(maxL, max(v, serie, false)) / pc_divider;
    });
    if (maxL > 1e10) {
      jL = jL.replace("auto", "mld");
    } else if (maxL > 1e7) {
      jL = jL.replace("auto", "mln");
    } else if (maxL > 1e4) {
      jL = jL.replace("auto", "tys.");
    } else {
      jL = jL.replace("auto", "");
    }
  }
  if (jednostka_prawa_os && jednostka_prawa_os?.indexOf("auto") > -1) {
    let maxR = 0;
    dane_xy?.forEach(v => {
      maxR = Math.max(maxR, max(v, serie, true)) / pc_divider;
    });
    if (maxR > 1e10) {
      jR = jR.replace("auto", "mld");
    } else if (maxR > 1e7) {
      jR = jR.replace("auto", "mln");
    } else if (maxR > 1e4) {
      jR = jR.replace("auto", "tys.");
    } else {
      jR = jR.replace("auto", "");
    }
  }
  const jednostka_divider = jL.indexOf('tys.') > -1?1e3:
    (jL.indexOf('mln') > -1?1e6:
      (jL.indexOf('mld') > -1?1e9:1));
  const numberDivider = (value: number) => {
    return Math.floor(value / (jednostka_divider * pc_divider));
  }
  const jednostka_divider_prawa_os = jR.indexOf('tys.') > -1?1e3:
    (jR.indexOf('mln') > -1?1e6:
      (jR.indexOf('mld') > -1?1e9:1));
  const numberDividerPrawaOs = (value: number) => {
    return Math.floor(value / (jednostka_divider_prawa_os * pc_divider));
  }
  return {jL, jR, numberDivider, numberDividerPrawaOs}
}