import React from "react";
import Dropdown, {Option} from "react-dropdown";
import {replaceStore} from "./index";
import {baza} from "./BazaInfografik";

interface Props {
}

export class Predefiniowane extends React.Component<Props> {
    render() {
      return <Dropdown className="predefiniowane" onChange={ev => {
          replaceStore(baza[ev.value] || '');
        }}
        value={"O MARNOWANIU YWNOCI"}
        options={Object.getOwnPropertyNames(baza).map(value => {return {label: value, value: value}})}/>
    }
}
