import {
    sample_gus,
    sample_kolowy,
    sample_kolumnowy,
    Wykres,
    wykresyGUSToStringIds
} from "./infografika/Wykres";
import React from "react";
import Box from "@material-ui/core/Box";
import {
    AddCircleOutline,
    ArrowDownward,
    ArrowUpward,
    CancelRounded,
    DonutLarge,
    Equalizer,
    ShowChart,
    PieChart
} from "@material-ui/icons";
import {DebounceInput} from "react-debounce-input";
import {RootState} from "./reducers";
import {
    addWykres,
    deleteWykres,
    moveDownWykres,
    moveUpWykres,
    setWykres
} from "./infografika/actions";
import {pobierzDaneGUS} from "./dane/actions";
import {connect, ConnectedProps} from "react-redux";
import {WykresXYDane} from "./wykresyUtil";
import {NameValue} from "./WykresKolowy";

function jstToString(id: number) {
    switch (id) {
        case 0:
            return "Polska";
        case 1:
            return "Makroregion";
        case 2:
            return "Wojewdztwo";
        case 3:
            return "Region";
        case 4:
            return "Podregion";
        case 5:
            return "Powiat";
        case 6:
            return "Gmina";
        case 7:
            return "miejsc.";
        default:
            return "NIEZNANY"
    }
}

class WykresyInternal extends React.Component<PropsFromRedux> {
    csvToWykresXYDaneArray(csv: string) {
        let lines = csv.split('\n');
        let result = [];
        for(let i = 0;i < lines.length;i++) {
            if (lines[i].length === 0) {
                continue;
            }
            let line = lines[i].split(",");
            let d0 = isNaN(Number(line[1]))?undefined:Number(line[1]);
            let d1 = isNaN(Number(line[2]))?undefined:Number(line[2]);
            let d2 = isNaN(Number(line[3]))?undefined:Number(line[3]);
            let d3 = isNaN(Number(line[4]))?undefined:Number(line[4]);
            let d4 = isNaN(Number(line[5]))?undefined:Number(line[5]);
            let d5 = isNaN(Number(line[6]))?undefined:Number(line[6]);
            result.push({label: line[0],
                dane0: d0,
                dane1: d1,
                dane2: d2,
                dane3: d3,
                dane4: d4,
                dane5: d5,
            });
        }
        return result;
    }

    wykresXYDaneArrayToCsv(json: WykresXYDane[]) {
        let ret = '';
        json.forEach((nameValue) => {ret += nameValue.label + ',' + (nameValue.dane0?nameValue.dane0:'') + ',' + (nameValue.dane1?nameValue.dane1:'') + ',' + (nameValue.dane2?nameValue.dane2:'') + ',' + (nameValue.dane3?nameValue.dane3:'') + ',' + (nameValue.dane4?nameValue.dane4:'') + ',' + (nameValue.dane5?nameValue.dane5:'') + '\n'});
        return ret;
    }

    csvToNameValueArray(csv: string) {
        let lines = csv.split('\n');
        let result = [];
        for(let i = 0;i < lines.length;i++) {
            let line = lines[i].split(",");
            if (line.length === 2) {
                let v = isNaN(Number(line[1]))?0:Number(line[1]);
                result.push({name: line[0], value: v});
            }
        }
        return result;
    }

    nameValueArrayToCsv(json: NameValue[]) {
        let x = '';
        json.forEach((nameValue) => {x += nameValue.name + ',' + nameValue.value + '\n'});
        return x;
    }

    renderChartType(index: number, wykres: Wykres) {
        const {setWykres} = this.props;
        let x: JSX.Element = <></>;
        let hint = undefined;
        switch (wykres.typ_wykresu) {
            case "Liniowy (XY)":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Kolumnowy (XY)"})}><Equalizer/></button>
                break;
            case "Kolumnowy (XY)":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Liniowy (XY)"})}><ShowChart/></button>
                break;
            case "GUS: Liniowy":
                hint = "Format: SERIA1,SERIA2:R (\":R\" przypisuje seri do prawej osi Y, a \":S:A\" zmienia wykres liniowy na powierzchniowy i pokazuje wartoci kumulatywnie)";
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "GUS: Kolumnowy"})}><Equalizer/></button>
                break;
            case "GUS: Kolumnowy":
                hint = "Format: SERIA1,SERIA2:R (\":R\" przypisuje seri do prawej osi Y, a \":S:A\" grupuje kolumny)";
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "GUS: Liniowy"})}><ShowChart/></button>
                break;
            case "Koowy":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Piercieniowy"})}><DonutLarge/></button>
                break;
            case "Piercieniowy":
                x = <button className="ikonaWykresu" onClick={ev=>setWykres(index, {typ_wykresu: "Koowy"})}><PieChart/></button>
                break;
        }
        return <React.Fragment>
            <Box display="flex" flexDirection="row">
                {x}
                <Box display="flex" flexDirection="column">
                    <span className="hint"> <b>Wykres {index}</b></span>
                    <DebounceInput size={60} type="text" name="opis" value={wykres.opis}
                                   onChange={ev=>setWykres(index, {opis: ev.target.value.trim()})}
                                   debounceTimeout={2000}/>
                </Box>
            </Box>

            {hint?<span className="hint">{hint}</span>:null}
            {wykres.typ_wykresu!=="Koowy"?<DebounceInput type="text" name="nazwy_serii" value={wykres.nazwy_serii}
                           onChange={ev=>setWykres(index, {nazwy_serii: ev.target.value.trim()})}
                           debounceTimeout={2000}/>:null}
        </React.Fragment>
    }

    renderWykres(index: number, wykres: Wykres, kod_gus: string) {
        const {pobierzDaneGUS, setWykres} = this.props;
        switch (wykres.typ_wykresu) {
            case "GUS: Liniowy":
            case "GUS: Kolumnowy":
                return <React.Fragment>
                    {this.renderChartType(index, wykres)}
                    <span className="hint">Identyfikatory danych z GUS, musz by posortowane wg numeru. Dane musz by dostpne na poziomie wybranego powiatu / gminy / wojewdztwa.</span>
                    <Box display="flex" flexDirection="row" alignItems="center">
                        <DebounceInput type="text" name="gus_ids" value={wykresyGUSToStringIds(wykres)}
                                       onChange={ev=>pobierzDaneGUS(index, kod_gus, ev.target.value.trim(), wykres.opis, wykres.nazwy_serii || '')}
                                       debounceTimeout={2000} size="57"/>
                        <span className="">&nbsp;&nbsp;&nbsp;lata:</span>
                        <DebounceInput onChange={(ev) => {setWykres(index, {gus_lata: Number(ev.target.value)})}} type="number" name="gus_lat" value={wykres.gus_lata} style={{width: "45px"}} min={1} max={10} debounceTimeout={2000}/>
                    </Box>
                    <table><thead>
                        <tr style={{fontStyle: "oblique"}}>
                            <td>id</td>
                            <td>poziom</td>
                            <td>jedn.</td>
                            <td>nazwa</td>
                            {/*<td>c.d.</td>*/}
                            {/*<td>{gus.n3}</td>*/}
                            <td>lata</td>
                        </tr>
                    </thead>
                        <tbody>
                            {wykresyGUSToStringIds(wykres).split(',').map((value, innerIndex) => {
                                if (wykres.gus === undefined || wykres.gus[Number(value).valueOf()] === undefined) {
                                    return <tr key={index + "-" + innerIndex}></tr>
                                }
                                const gus = wykres.gus[Number(value).valueOf()];
                                return <tr key={index + "-" + innerIndex}>
                                    <td>{value}</td>
                                    <td>{jstToString(Number(gus.level || -1))}</td>
                                    <td>{gus.measureUnitName}</td>
                                    <td title={gus.n1 + (gus.n2?(" : " + gus.n2 + (gus.n3?(" : " + gus.n3):'')):'')}>
                                        {gus.n1?(gus.n1.substr(0, 25) + (gus.n1.length > 25?'...':'')):'BRAK'}
                                    </td>
                                    <td>{gus.years?gus.years[0]:''}-{gus.years?gus.years[gus.years.length-1]:''}</td>
                                </tr>
                            })}
                        </tbody>
                    </table>
                </React.Fragment>
            case "Kolumnowy (XY)":
            case "Liniowy (XY)":
                return <React.Fragment>
                    {this.renderChartType(index, wykres)}
                    <DebounceInput element="textarea" forceNotifyByEnter={false} debounceTimeout={2000} className="json" name="wlasny_liniowy.dane"
                                   onChange={ev=>setWykres(index, {dane_xy: this.csvToWykresXYDaneArray(ev.target.value)})}
                                   value={this.wykresXYDaneArrayToCsv(wykres.dane_xy || [])}/>
                    <span className="hint">Jednostka osi Y. Jeli zawiera 'tys.' lub 'mln' to zostanie odpowiednio przeliczona:</span>
                    <DebounceInput type="text" name="wlasny_liniowy.jednostka" onChange={ev=>setWykres(index, {jednostka: ev.target.value})}
                           value={wykres.jednostka} debounceTimeout={2000}/>
                </React.Fragment>
            case "Koowy":
            case "Piercieniowy":
                return <React.Fragment>
                    {this.renderChartType(index, wykres)}
                    <span className="hint">Dane do wykresu:</span>
                    <DebounceInput element="textarea" forceNotifyByEnter={false} debounceTimeout={2000} className="json" name="wlasny.dane"
                                   onChange={ev=>setWykres(index, {dane_kolowy: this.csvToNameValueArray(ev.target.value)})} value={this.nameValueArrayToCsv(wykres.dane_kolowy || [])}/>
                    <span className="hint">Jednostka osi Y. Jeli zawiera 'tys.' lub 'mln' to zostanie odpowiednio przeliczona:</span>
                    <DebounceInput type="text" name="wlasny.jednostka" onChange={ev=>setWykres(index, {jednostka: ev.target.value})}
                                   value={wykres.jednostka} debounceTimeout={2000}/>
                </React.Fragment>
        }
        return null;
    }

    addGUSWykres() {
        console.log(this);
        const {addWykres, pobierzDaneGUS, dane_gminy, wykresy} = this.props;
        const wykres = sample_gus[Math.floor(Math.random() * sample_gus.length)];
        const id = wykresy.length;
        addWykres(wykres);
        pobierzDaneGUS(id, dane_gminy.kod_gus || '', wykresyGUSToStringIds(wykres) || '', wykres.opis, wykres.nazwy_serii || '');
    }

    render() {
        const {dane_gminy, wykresy} = this.props;
        const {moveUpWykres, deleteWykres, moveDownWykres, addWykres} = this.props;

        return (<React.Fragment>
            {wykresy.map((input: Wykres, index: number) => {
                return <React.Fragment key={index}>
                    <Box display="flex" flexDirection="row" className={index%2===0?"odd":"even"}>
                        <Box display="flex" flexDirection="column" className="panelIkon">
                            <button className="ikonaWykresu" onClick={ev=>moveUpWykres(index)} style={{visibility: index > 0?"visible":"hidden"}}><ArrowUpward/></button>
                            <button className="ikonaWykresu czerwony" onClick={ev=>deleteWykres(index)}><CancelRounded/></button>
                            <button className="ikonaWykresu" onClick={ev=>moveDownWykres(index)} style={{visibility: index < wykresy.length - 1?"visible":"hidden"}}><ArrowDownward/></button>
                        </Box>
                        <Box display="flex" flexDirection="column">
                            {this.renderWykres(index, input, dane_gminy.kod_gus || '')}
                        </Box>
                    </Box>
                </React.Fragment>})}
                <Box display="flex" flexDirection="row" className={"panelDodania " + ((wykresy.length)%2===0?"odd":"even")}>
                    Dodaj wykres:
                    <button className="ikonaWykresu" onClick={ev=>addWykres(sample_kolumnowy)}><AddCircleOutline/><Equalizer/></button>
                    <button className="ikonaWykresu" onClick={ev=>addWykres(sample_kolowy)}><AddCircleOutline/><PieChart/></button>
                    <button className="ikonaWykresu" onClick={ev=>{this.addGUSWykres()}}><AddCircleOutline/>GUS</button>
                </Box>
            </React.Fragment>);
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        wykresy: state.wykresy,
        dane_gminy: state.dane_gminy,
    };
};

const mapDispatchToProps = {
    pobierzDaneGUS,
    moveUpWykres,
    deleteWykres,
    moveDownWykres,
    setWykres,
    addWykres,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(WykresyInternal);