import {DaneGminy} from "./DaneGminy";
import {CHANGE_DANE_GMINY, CHANGE_DANE_PODREGIONU, CHANGE_POROWNAWCZE_DANE_GMINY} from "../action";
import { setup } from 'axios-cache-adapter'
import axios, {AxiosResponse} from "axios";
import {Dispatch} from "react";
import {DanePodregionu} from "./DanePodregionu";
import {WykresXYDane} from "../wykresyUtil";
import {setWykres, usunSerieGUS} from "../infografika/actions";
import {SeriaGUS, Wykres, wykresyGUSToStringIds} from "../infografika/Wykres";
import {changePorownawczyKodGminy} from "../konfiguracja/actions";

// Create `axios` instance with pre-configured `axios-cache-adapter` attached to it
const api = setup({
    // `axios` options
    baseURL: 'https://bdl.stat.gov.pl/api/v1/',

    // `axios-cache-adapter` options
    cache: {
        maxAge: 365 * 24 * 60 * 60 * 1000
    }
})

export const pobierzDaneGminy = (kodGminy: string, porownawcze: boolean = false) => {
    return async function (dispatch: Dispatch<any>, getState: any) {
        const apiUrl = '/files/' + kodGminy + '-panel.json';
        await axios.get(apiUrl).then((response: AxiosResponse<DaneGminy>) => {
            if (response.data === undefined || response.data.ludnosc === undefined) {
                return;
            }
            if (porownawcze) {
                setTimeout(() => dispatch(changePorownawczeDaneGminy(response.data)), 1500);
                return;
            }
            dispatch(changeDaneGminy(response.data));
            if (response.data.eurostat !== undefined) {
                // w/o timeout there's no rendering of porownawcze
                setTimeout(() => dispatch(pobierzZgony(response.data.eurostat || '')), 1000);
            }
            if (response.data.kod_gus) {
                getState().wykresy.forEach((input: Wykres, index: number) => {
                    // w/o timeout there's no rendering of porownawcze
                    setTimeout(() => dispatch(pobierzDaneGUS(index, response.data.kod_gus || '', wykresyGUSToStringIds(input) || '', input.opis || '', input.nazwy_serii || '')), 1000);
                });
            }

            let doPorownania = '';
            // Powiaty i wojewodztwa porwnujemy z wojewdztwem
            if (kodGminy.slice(4, 6) === '00') {
                doPorownania = kodGminy;
            } else if (response.data.ludnosc <= 5000) {
               doPorownania = 'do 5000';
            } else if (response.data.ludnosc <= 20000) {
                doPorownania = 'do 20000';
            } else if (response.data.ludnosc <= 50000) {
                doPorownania = 'do 50000';
            } else {
                doPorownania = 'powyej 50000'
            }
            setTimeout(() => dispatch(changePorownawczyKodGminy(doPorownania)), 2000);
            setTimeout(() => dispatch(pobierzDaneGminy(doPorownania, true)), 1000);
            return;
        });
    }
}

export const pobierzZgony = (eurostat: string) => {
    return async function(dispatch: Dispatch<any>, getState: any) {
        const apiUrl = '/files/' + eurostat + '.json';
        axios.get(apiUrl).then((response) => {
            dispatch(changeDanePodregionu(response.data));
        });
    }
}

export const pobierzDaneGUS = (index: number, kodGus: string, ids_string: string, opis: string, nazwy_serii: string) => {
    return async function(dispatch: Dispatch<any>, getState: any) {
        if (ids_string === '') {
            return null;
        }
        const ids = ids_string.split(",").map(x => parseInt(x));
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        // Usuwamy przestarzae idki:
        dispatch(usunSerieGUS(ids, index));

        ids.forEach(id => {
            const varUrl = 'https://bdl.stat.gov.pl/api/v1/variables/' + id + '?lang=pl';
            api.get(varUrl).then((response) => {
                const seriaGus:SeriaGUS = response.data;
                dispatch(setWykres(index, {gus: {[id]: seriaGus}}));
            });
            apiUrl += "&var-id=" + id;
        });
        api.get(apiUrl).then((response) => {
            let dane: WykresXYDane[] = [{label: "2010"}, {label: "2011"}, {label: "2012"}, {label: "2013"}, {label: "2014"}, {label: "2015"}, {label: "2016"}, {label: "2017"}, {label: "2018"}, {label: "2019"}];
            for (let r of response.data.results) {
                for (let i = 0; i < ids.length; i++) {
                    // znajdzmy odpowiedni id do przypisania wartosci, aby byly w takiej kolejnosci jak na liscie
                    if (ids[i] !== r.id) {
                        continue;
                    }
                    for (let v of r.values) {
                        for (let z of dane) {
                            if (z.label === v.year) {
                                // @ts-ignore
                                z["dane" + i] = v.val;
                            }
                        }
                    }
                }
            }
            const wykres = {opis: opis, nazwy_serii: nazwy_serii, dane_xy: dane};
            dispatch(setWykres(index, wykres));
        });
    }
}

export const changePorownawczeDaneGminy = (dane_gminy: DaneGminy) => ({
    type: CHANGE_POROWNAWCZE_DANE_GMINY,
    payload: dane_gminy
});

export const changeDaneGminy = (dane_gminy: DaneGminy) => ({
    type: CHANGE_DANE_GMINY,
    payload: dane_gminy
});

export const changeDanePodregionu = (dane_podregionu: DanePodregionu) => ({
    type: CHANGE_DANE_PODREGIONU,
    payload: dane_podregionu
})