import {
    ADD_WYKRES,
    CHANGE_GRUPA_DZIAL,
    DELETE_WYKRES,
    MOVE_DOWN_WYKRES,
    MOVE_UP_WYKRES,
    SET_WYKRES,
    USUN_SERIE_GUS
} from "../action";
import { CHANGE_GRUPA_ROZDZIAL } from "../action";
import {Wykres} from "./Wykres";

export const changeGrupaDzial = (grupa: string) => ({
    type: CHANGE_GRUPA_DZIAL,
    payload: grupa,
});

export const changeGrupaRozdzial = (grupa: string) => ({
    type: CHANGE_GRUPA_ROZDZIAL,
    payload: grupa,
});

export const addWykres = (wykres: Wykres) => ({
    type: ADD_WYKRES,
    payload: {wykres: wykres},
})

export const deleteWykres = (index: number) => ({
    type: DELETE_WYKRES,
    payload: {index: index},
})

export const setWykres = (index: number, wykres: Partial<Wykres>) => ({
    type: SET_WYKRES,
    payload: {wykres: wykres, index: index},
})

export const moveUpWykres = (index: number) => ({
    type: MOVE_UP_WYKRES,
    payload: {index: index},
})

export const moveDownWykres = (index: number) => ({
    type: MOVE_DOWN_WYKRES,
    payload: {index: index},
})

export const usunSerieGUS = (pozostale_serie_gus: number[], index: number) => ({
    type: USUN_SERIE_GUS,
    payload: {pozostale_serie_gus: pozostale_serie_gus, index: index},
})