import React from "react";
import {Box} from "@material-ui/core";
import {
  Bar,
  BarChart,
  CartesianGrid, LabelList,
  XAxis,
  YAxis
} from "recharts";

import {Wykres} from "./infografika/Wykres";
import {COLOR_MAP} from "./wykresyUtil";
import {NameValue} from "./WykresKolowy";

interface WykresPorownawczyProps extends Wykres {
  porownawcze_dane_kolowy: NameValue[];
  porownawcze_ludnosc: number;
}

export class WykresPorownawczy extends React.Component<WykresPorownawczyProps> {
  render() {
    const {dane_kolowy = [], porownawcze_dane_kolowy, opis = "Wydatki w porwnaniu do innych jednostek", jednostka = "auto", kolory = 'paleta', per_capita = true, ludnosc, porownawcze_ludnosc} = this.props;
    if (opis === '' || porownawcze_dane_kolowy === undefined || porownawcze_dane_kolowy.length === 0) {
      return null;
    }
    let daneTrimmed: NameValue[] = dane_kolowy.slice(0, 5);
    const pc_divider = per_capita?ludnosc || 1:1;
    const pc_divider2 = per_capita?porownawcze_ludnosc || 1:1;
    let daneInter: object[] = [];
    const max = daneTrimmed.map(value => value.value).reduce((preVal, curVal) => Math.max(preVal, curVal), 0);
    const max2 = porownawcze_dane_kolowy.map(value => value.value).reduce((preVal, curVal) => Math.max(preVal, curVal), 0);
    daneTrimmed.forEach((entry, index) => {
      const dane2 = porownawcze_dane_kolowy.filter(value => value.name === entry.name);
      daneInter.push({
        labelka: entry.name,
        gmina1: entry.value / pc_divider,
        gmina2: (dane2.length > 0 ? dane2[0].value : 0) / pc_divider2,
        // Using space as a key prevents from it showing up in the legend
        max: Math.max(max / pc_divider, max2 / pc_divider2)})
      }
    );
    const daneFinal = daneInter.map(value => {
      // @ts-ignore
      return {...value, diff: (value.gmina1 > value.gmina2?"+":"") +
          // @ts-ignore
          Math.round(((value.gmina1 / value.gmina2) - 1) * 100) + "%"}
    });
    const commonChartProps = {
      syncId: opis,
      width: 1200,
      height: 600,
      data: daneFinal,
      margin: {top: 10, right: 30, left: 0, bottom: 10},
    }
    const grid = () => { return <CartesianGrid strokeDasharray="5 5"/> }
    const x = () => { return <XAxis type="number" orientation="bottom" xAxisId="bottom" tickSize={12} padding={{ left: 0, right: 50 }} interval="preserveEnd" label={{value: jednostka, position: "right", offset: 0}} />}
    const y = () => { return <YAxis type="category" dataKey="bleh" axisLine={{strokeWidth: 2}} tickSize={8} /> }
    // const legend = () => { return <Legend align="left" layout="horizontal" iconSize={20} wrapperStyle={{bottom: -10}}/> }
    return <Box><br/><Box margin="40px">{opis}</Box>
      <BarChart {...commonChartProps} layout='vertical' barGap="0">
        {grid()} {x()} {y()}
        <Bar key={"porownawcze_dane-0"}
             stackId={0}
             dataKey="max" fill="#0000" xAxisId="bottom" barSize={30}>
          <LabelList dataKey="labelka" position="insideBottomLeft"/>
        </Bar>
        <Bar key={"porownawcze_dane-2"}
             barSize={12}
             stackId={2}
             layout="horizontal"
             name="gmina2"
             dataKey={"gmina2"}
             xAxisId="bottom"
             fill='#bbb'/>
        <Bar key={"porownawcze_dane-1"}
             stackId={1}
             layout="horizontal"
             name={"gmina1"}
             dataKey={"gmina1"}
             fill={COLOR_MAP[kolory][daneTrimmed.length].reverse()[0]}
             xAxisId="bottom"
             barSize={20}
        >
          <LabelList dataKey="diff" position="right"/>
        </Bar>
      </BarChart>
    </Box>;
  }
}