import React from "react";
import {pobierzDaneGminy} from "./dane/actions";
import {connect, ConnectedProps} from "react-redux";
import {RootState} from "./reducers";
import {changeKodGminy, changePorownawczyKodGminy} from "./konfiguracja/actions";
import Dropdown, {Option} from 'react-dropdown';

interface SelektorGminyInternalProps extends PropsFromRedux {
  porownawcze: boolean;
}

class SelektorGminyInternal extends React.Component<SelektorGminyInternalProps> {
    render() {
      const {pobierzDaneGminy, dane_gminy, porownawcze_dane_gminy, lista_powiatow, changeKodGminy, changePorownawczyKodGminy, porownawczy_kod_gminy, kod_gminy, wojewodztwo, porownawcze = false} = this.props;
      if (lista_powiatow === undefined) {
        return <React.Fragment />;
      }
      let optionList: Option[] = [];
      if (porownawcze) {
        optionList.push({label: 'gminy do 5000 mieszkacw', value: 'do 5000'});
        optionList.push({label: 'gminy do 20000 mieszkacw', value: 'do 20000'});
        optionList.push({label: 'gminy do 50000 mieszkacw', value: 'do 50000'});
        optionList.push({label: 'gminy powyej 50000 mieszkacw', value: 'powyej 50000'});
      }

      optionList.push({label: <b>{wojewodztwo}</b>, value: kod_gminy.substr(0, 2) + "00000"});
      lista_powiatow.forEach((value) => {
        optionList.push({label: <b>{value.powiat}</b>, value: value.gminy[0].kod_gminy.substr(0, 4) + "000"});
        value.gminy.forEach((gmina) => {
          optionList.push({label: gmina.gmina, value: gmina.kod_gminy});
        });
      });
      return <Dropdown onChange={ev => {
          porownawcze?changePorownawczyKodGminy(ev.value):changeKodGminy(ev.value);
          pobierzDaneGminy(ev.value, porownawcze);
        }}
        value={{
          label: porownawcze?porownawcze_dane_gminy.gmina:dane_gminy.gmina,
          value: porownawcze?porownawczy_kod_gminy:kod_gminy
        }}
        options={optionList}/>
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        lista_powiatow: state.lista_powiatow,
        kod_gminy: state.kod_gminy,
        porownawczy_kod_gminy: state.porownawczy_kod_gminy,
        wojewodztwo: state.wojewodztwo,
        dane_gminy: state.dane_gminy,
        porownawcze_dane_gminy: state.dane_gminy,
    };
};

const mapDispatchToProps = {
    changePorownawczyKodGminy,
    pobierzDaneGminy,
    changeKodGminy,
}

const connector = connect(
    mapStateToProps,
    mapDispatchToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(SelektorGminyInternal);