export function wykresyGUSToStringIds(wykres: Wykres) {
    return wykresyGUSToIdArray(wykres).toString();
}

export function wykresyGUSToIdArray(wykres: Wykres) {
    if (wykres === undefined || wykres.gus === undefined) {
        return [];
    }
    return Object.getOwnPropertyNames(wykres.gus).filter(value => !isNaN(+value) && wykres.gus && wykres.gus[+value].id === +value);
}