import React from "react";
import {Box} from "@material-ui/core";
import {
    Area, AreaChart,
    Bar,
    BarChart,
    CartesianGrid,
    Legend,
    Line,
    LineChart,
    XAxis,
    YAxis
} from "recharts";

import {Wykres} from "./infografika/Wykres";
import {COLOR_MAP, jednostkiAndDivider} from "./wykresyUtil";

export class WykresXY extends React.Component<Wykres> {
    render() {
        const {dane_xy, opis, nazwy_serii, typ_wykresu = 'Liniowy (XY)', jednostka = "auto", jednostka_prawa_os = "auto", kolory = 'paleta', gus_lata, per_capita, ludnosc} = this.props;
        if (opis === '') {
            return null;
        }
        // We only have up to 9 colors, so we have to limit # of series
        let serie = nazwy_serii?nazwy_serii.split(",").slice(0, 9):[];
        const colors = COLOR_MAP[kolory];
        const commonChartProps = {
            syncId: opis,
            width: 1200,
            height: 500,
            // Filtrujemy dane wg tego ile lat chcemy pokaza
            data: gus_lata?dane_xy?.filter(value => {
                return Number(value.label) >= (2020 - gus_lata) &&
                  (value.dane0 || value.dane1 || value.dane2 || value.dane3 || value.dane4 || value.dane5 || value.dane6 || value.dane7 || value.dane8 || value.dane9)
            }):dane_xy,
            margin: {top: 10, right: 40, left: 45, bottom: 10}
        }

        const pc_divider = per_capita?ludnosc || 1:1;
        const {jL, jR, numberDivider, numberDividerPrawaOs} = jednostkiAndDivider(dane_xy || [], serie, jednostka, jednostka_prawa_os, pc_divider);
        const grid = () => { return <CartesianGrid strokeDasharray="5 5"/> }
        const x = () => { return <XAxis dataKey="label" tickSize={12} tickMargin={10}/>}
        const y1 = () => { return <YAxis yAxisId="left" orientation="left" axisLine={{strokeWidth: 2}} tickSize={8} label={{value: jL, position: "bottom", offset: 50}} tickFormatter={numberDivider}/> }
        const y2 = () => { return (nazwy_serii && nazwy_serii.indexOf(':R') > -1)?<YAxis yAxisId="right" orientation="right" axisLine={{strokeWidth: 2}} tickSize={18} label={{value: jR, position: "bottom", offset: 50}} tickFormatter={numberDividerPrawaOs}/>:null }
        const legend = () => { return <Legend iconSize={20} wrapperStyle={{bottom: -10}}/> }
        return <Box><br/><Box margin="40px">{opis}</Box>
            {typ_wykresu.indexOf("Kolumnowy") > -1?
                <BarChart {...commonChartProps}>
                    {grid()} {x()} {y1()} {y2()} {legend()}
                    {serie.map((value, index) => {
                        return value !== ''?<Bar key={index}
                                    name={value.replace(':R', ' (prawa o)').replace(/:S:./, '')}
                                    stackId={value.indexOf(':S:') > -1?value.substr(value.indexOf(':S:') + 3, 1):undefined}
                                    dataKey={"dane" + index}
                                    fill={colors[serie.length][index]}
                                    yAxisId={value.indexOf(':R') > -1?'right':'left'}/>:null;
                    })
                    }
                </BarChart>:
                nazwy_serii && nazwy_serii.indexOf(":S") === -1?
                    <LineChart {...commonChartProps}>
                        {grid()} {x()} {y1()} {y2()} {legend()}
                        {serie.map((value, index) => {
                            return value !== ''?<Line key={index}
                                        type="monotone"
                                        name={value.replace(':R', ' (prawa o)')}
                                        dataKey={"dane" + index}
                                        stroke={colors[serie.length][index]}
                                        fill={colors[serie.length][index]}
                                        strokeWidth={2}
                                        yAxisId={value.indexOf(':R') > -1?'right':'left'}/>:null;
                        })
                        }
                    </LineChart>:
                    <AreaChart {...commonChartProps}>
                        {grid()} {x()} {y1()} {y2()} {legend()}
                        {serie.map((value, index) => {
                            return value !== ''?<Area key={index}
                                                      type="monotone"
                                                      stackId={value.indexOf(':S:') > -1?value.substr(value.indexOf(':S:') + 3, 1):undefined}
                                                      name={value.replace(':R', ' (prawa o)').replace(/:S:./, '')}
                                                      dataKey={"dane" + index}
                                                      stroke={colors[serie.length][index]}
                                                      fill={colors[serie.length][index]}
                                                      strokeWidth={2}
                                                      yAxisId={value.indexOf(':R') > -1?'right':'left'}/>:null;
                        })
                        }
                    </AreaChart>
            }
        </Box>;
    }
}